@import "styles/colors";
@import "styles/fonts";

.shrine {
  line-height: 48px;
  .counter {
    text-align: right;
  }
  .diff {
    @include f-reg;
    &.is-ahead {
      color: $warm-green;
    }
    &.is-pb {
      @include gold;
    }
  }
  .time {
    @include f-reg;
    text-align: right;
    color: $pale-blue;
    padding: 0 4px 0 0;
  }
}

.shrine.infolog {
  .counter {
    margin: 0 0 0 auto;
  }
}

.shrine.current {
  margin: 24px auto;
  .counter {
    grid-row: 1/3;
  }
  .name {
    @include f-maj;
    line-height: 48px;
  }
  .desc {
    @include f-sec;
    line-height: 32px;
    grid-row: 2;
    grid-column: 2 / 3;
  }
  .time {
    @include f-majsec;
    color: $pale-blue;
    grid-column: 3/5;
    grid-row: 1/3;
    &.is-paused, &.is-initial {
      color: $cloudy-gray;
    }
    .delta {
      @include f-reg;
      margin: 12px 0 -12px;
    }
  }
}
